


.SideMainContainer{

      height: calc( 100vh - var(--headerHeight) - var(--sideToolBarHeight));
      width: var(--sideNavBarWidth);
      position: fixed;
      z-index: 1;
      top:calc( var(--headerHeight) + var(--sideToolBarHeight) );
      left: 0;
      background-color: var(--sideNavBarBkgColor);
      overflow-x: hidden;
      transition: 0.5s;
      padding-top: 35px;
      /*box-shadow: 5px 5px 5px rgb(64, 64, 64) inset;*/
      -ms-overflow-style: none;  /* IE and Edge */
      scrollbar-width: none;


}


.SideMainContainer::-webkit-scrollbar {
  display: none;
}



