
.UsersIcon{

    font-size: calc( 0.4 * var(--headerHeight) );;
    color: var(--headerRightIconsColor);
    vertical-align:middle;
    }


.UsersIcon:hover{
   color: var(--themeColor)

}



.ActionItemsButton{

    height: calc( 0.65 * var(--headerHeight) );
    width: calc( 0.65 * var(--headerHeight) );
    border-style: solid;
    border-width:medium;
    border-color: var(--headerRightIconsColor);
    border-radius: 50%;
    background-color: var(--themeColor);
    vertical-align: middle;
    cursor:pointer;
    margin-left: 20px;
    display:flex;
    align-items: center;
    justify-content: center;
    }

.ActionItemsButton:hover{

background-color:white;

}

