
.UsersIcon{

    font-size: calc( 0.40 * var(--headerHeight) );;
    color: var(--headerRightIconsColor);
    vertical-align:middle;
    }


.UsersIcon:hover{
   color: var(--themeColor)

}    



.ActionItemsButton{

    height: calc( 0.65 * var(--headerHeight) );
    width: calc( 0.65 * var(--headerHeight) );
    border-style: solid ;
    border-color: var(--headerRightIconsColor);
    border-width:medium;
    border-radius: 50%;
    background-color: var(--themeColor);
    vertical-align: middle;
    cursor:pointer;
    display:flex;
    align-items: center;
    justify-content: center;
    }

.ActionItemsButton:hover{

background-color:var(--headerRightIconsColor);

}




