@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,300i,400,400i,600,600i,700,700i|Roboto:300,300i,400,400i,500,500i,600,600i,700,700i|Poppins:300,300i,400,400i,500,500i,600,600i,700,700i');

@import url("../../App.css");

.headerLeft{

    text-align: center;
    padding-left: 40px;
    display: flex;
    overflow: hidden;
    }

.logo{
    width: var(--headerLogoWidth);
    height: var(--headerLogoWidth);
    border-radius: 50%;
    position: relative;
    /*following lines are to align the image vertically in the middle*/
    top: 50%;
    -webkit-transform: perspective(1px) translateY(-50%);
    -ms-transform: perspective(1px) translateY(-50%);
    transform: perspective(1px) translateY(-50%);
    }

.courseName{

    margin-left: 1rem;
    text-decoration: none;
    color: white;
    font-weight: lighter;
    font-style: normal;
    }


.superScript{
    position: relative;	
    vertical-align: top; 
    font-size: 0.6em;
    top: -0.5em; 
    }



.ExpConButton{
font-size: 20px;
height: calc( 0.7 * var(--headerHeight));
position: relative;
top: 50%;
-webkit-transform: perspective(1px) translateY(-50%);
-ms-transform: perspective(1px) translateY(-50%);
transform: perspective(1px) translateY(-50%);
left: 10px;
border-style: none;
background-color: #1e458a;
color: white;
cursor: pointer;
}


