



 .sideNavBarButton {
      padding: 10px;
      text-decoration: none;
      font-size: var(--sideNavBarFontSize);;
      color: var(--sideNavBarIconColor);
      background-color:var(--sideNavBarBkgColor);
      border-style: solid;
      border-color:#757575;
      border-width: thin;
      display: block;
      font-style: normal;
      transition: 0.3s;
      font-weight: 100;
      margin:5px;
      width: var(--sideNavBarWidth);
      text-align: left;
      cursor: pointer;
      position:relative;
      top: -35px;
      align-items: center;
    }


.sideNavBarButton:hover{

background: linear-gradient(to right, var(--sideNavBarBtnhoverColor) 90%, var(--themeColor) 0%) !important;

}


.sideNavBarButton::-webkit-scrollbar {
      display: none;
    }

.sideNavBarButton i {
      font-style: normal;
      font-family: sans-serif;
      padding-left: 0px;
      font-size: var(--sideNavBarFontSize);
    }
    


.buttonText{
color: var(--sideNavBarButtonTextColor);
vertical-align: middle;
}


.dashIcon{
vertical-align: middle;
padding-right: 5px;
font-size:var(--sideNavBarFontSize);
}


