html{

     /*global color codes*/
     --themeColor: #224C98;
     --headerIconColor:#9fbaed;
     --headerIconColorOnhover:white;
     --sideNavBarIconColor: white;
     --sideNavBarBkgColor: #505050; 
     --sideNavBarBtnhoverColor: #686868;
     --sideNavBarButtonTextColor:white; 

     --userProfileInfoBoxBkgColor: white;
   
     --mainAppContainerBkgColor: #E8E8E8;



     --headerHeight: 3.8rem;
     --headerFontSize: calc( 0.35 * var(--headerHeight) );
     --headerLogoWidth: calc( 0.80 * var(--headerHeight));
     

     --sideNavBarWidth: calc( 3.2 * var(--headerHeight)) ;
     --sideNavBarWidthOnContract: calc( 0.22 * var(--sideNavBarWidth) );
     --sideNavBarWidthMobile: 3.4rem;
     --sideNavBarFontSize: calc( 0.33 * var(--headerHeight) );
     --headerLogoWidth: calc( 0.80 * var(--headerHeight));

     --headerRightIconsColor: #9fbaed;

     --sideToolBarHeight: calc(0.77 * var(--headerHeight));
     --topInfoBarInstructorWidth:calc( 7.0 * var(--headerHeight)); 
}



@import url('https://fonts.googleapis.com/css?family=Open+Sans:300,300i,400,400i,600,600i,700,700i|Roboto:300,300i,400,400i,500,500i,600,600i,700,700i|Poppins:300,300i,400,400i,500,500i,600,600i,700,700i');








.edrapp{

     width:100%;
     background-color: white;

}


/* default screen resolution designed for: 1920x1080*/
/*1700px - 1950px: header:4.5rem*/





@media screen and (max-width: 1050px) {

html{
--headerHeight: 3.0rem;
 }
}





/* second most popular screen resolution: 1366x768*/
@media screen and (min-width: 1050px) and (max-width: 1360px) {

html{
--headerHeight: 3.5rem;

 }
}



/*third most popular screen resolution: 1440x900*/

/* third most popular screen resolution : 1536x864*/

@media screen and (min-width: 1360px)  and (max-width:1700px){

html{
--headerHeight: 3.8rem;

 }
}


/*1700px - 1950px: header:4.5rem*/





@media screen and (min-width: 1950px)  and (max-width:2300px){

html{
--headerHeight: 5.0rem;

 }
}



@media screen and (min-width: 2300px)  and (max-width:2500px){

html{
--headerHeight: 5.7rem;

 }
}






/* retina display mac: 2560x1440*/

@media screen and (min-width: 2500px) {

html{
--headerHeight: 6.5rem;
}

}


@media screen and (min-width: 3000px) {

html{
--headerHeight: 10.5rem;
}

}



