
.profileImageInfo{
height: 40%;
padding:10%;
padding-top: 0px;
background-color: white;
text-align: center;
color: black;
display: flex;
flex-direction: column;
}

.nameDiv{
height: 40%;
background-color: white;
}

.name_i{

font-style: normal;
color:var(--sideNavBarBkgColor)
}

.profileImageInfo_position{

height:20%;
background:linear-gradient(to top, #ddd 98%, grey 0%);
display: flex;
justify-content: center;
align-items: center;
font-size: calc( 0.30 * var(--headerHeight) );
}

.profileImageInfo_position i{

font-style: normal;
position:relative;
left: 10px;
font-size: calc( 0.30 * var(--headerHeight) );
}


.profileImageInfo_joiningDate{

height:20%;
background:linear-gradient(to top, #ddd 98%, grey 0%);
display: flex;
justify-content: center;
align-items: center;
font-size: calc( 0.30 * var(--headerHeight) );
}


.profileImageInfo_joiningDate i{
font-style: normal;
position:relative;
left: 10px;
font-size: calc( 0.30 * var(--headerHeight) );
}

.profileImageInfo_status{
height:20%;
background:linear-gradient(to top, #ddd 98%, grey 0%);
display: flex;
justify-content: center;
align-items: center;
font-size: calc( 0.30 * var(--headerHeight) );
}

.profileImageInfo_status i{

font-style: normal;
color: #4CBB17;
position:relative;
left: 10px;
font-size: calc( 0.30 * var(--headerHeight) );
}



